.container {
  background-image: url(/ISS-Live-Location/./static/media/photo1.6738d770.jpg);
  height: 100vh;
  width: 100vw;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-family: 'Space Mono', monospace;
}

.actionbutton {
  width: 150px;
  height: 150px;
  position: fixed;
  bottom: 10%;
  border: 2px solid lightsteelblue;
  border-radius: 100%;
  font-size: 25px;
  background-color: transparent;
  color: white;
  font-family: 'Space Mono', monospace;
}

.actionbutton:focus {
  outline: 0;
}
.actionbutton:active {
  border-color: rgba(176, 196, 222, 0.5);
}
.actionbutton:hover {
  background-color: rgba(242, 242, 242, 0.1)
}

.planet {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  background-image: url(/ISS-Live-Location/./static/media/map2.f2e22427.png);
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -290px;
  margin-left: -170px;
}
.gravity {
  width: 10px;
  height: 10px;
  -webkit-animation: sideToSide 3s infinite ease-in-out alternate;
          animation: sideToSide 3s infinite ease-in-out alternate;
  position: absolute;
  top: 33%;
  left: 33%;
  margin-left: -135px;
}
.satellite {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-image: url(/ISS-Live-Location/./static/media/satellite.12b8c75d.svg);
  -webkit-animation: circle 6s infinite linear;
          animation: circle 6s infinite linear;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -200px;
  -webkit-transform-origin: 200px center;
          transform-origin: 200px center;
}
@-webkit-keyframes circle { to { -webkit-transform: rotate(1turn); transform: rotate(1turn); } }
@keyframes circle { to { -webkit-transform: rotate(1turn); transform: rotate(1turn); } }
@-webkit-keyframes sideToSide { to { -webkit-transform: translateX(600px); transform: translateX(600px); } }
@keyframes sideToSide { to { -webkit-transform: translateX(600px); transform: translateX(600px); } }
